@import "variables";
* {
    font-family: Poppins, sans-serif;
}
@media only screen and (max-width: 600px) {
    body {
        background-color: $pgBgColor;
        margin: 0;
    }
    .container {
        padding: $marginSize_20;
        margin-top: $navHeight;
    }
    nav {
        box-shadow: 0 4px 40px rgba(0, 0, 0, 0.12);
        background: $white;
        height: $navHeight;
        display: flex;
        width: 100%;
        overflow: hidden;
        position: fixed;
        top: 0;
        align-items: center;
        padding-left: 20px;
    }
    .menu_bar {
        width: $marginSize_20;
        height: $marginSize_20;
    }
    .menu_items {
        width: 80%;
        display: flex;
        justify-content: end;
    }
    .divider {
        height: 80%;
        border: 1px solid #dfe0eb;
        margin: 2px 10px;
    }
    .profile_name {
        margin-top: $liTopMargin;
        margin-right: 8px;
        font-family: Poppins, sans-serif;
        font-style: normal;
        font-weight: 300;
        font-size: 12px;
        line-height: 20px;
    }
    .notificationImage {
        width: $marginSize_20;
        margin-top: $liTopMargin;
        vertical-align: center;
    }
    .profileImage {
        margin-right: 15px;
        border-radius: 100px;
    }
    .menu_items li {
        display: inline-block;
        justify-content: center;
    }
    .grid_container {
        display: grid;
        grid-column-gap: 50px;
        justify-content: space-between;
        grid-template-columns: auto auto;
    }

    .grid_containerBox {
        display: grid;
        grid-gap: 10px;
        grid-template-columns: repeat(2, 1fr);;
        margin-top: $marginSize_20;
    }
    .grid_itemBox {
        height: 100px;
        padding: 20px 10px;
    }
    .box {
        background: #FFFFFF;
        min-height: 50px;
        box-shadow: 0 12px 26px rgba(16, 30, 115, 0.06);
        border-radius: 4px;
    }
    .box h6 {
        font-family: Poppins, sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        letter-spacing: -0.02em;
        color: #A3AED0;
        margin: $marginSize_20 0 0 0;
    }
    .box h3 {
        font-family: PoppinsBold, sans-serif;
        letter-spacing: -0.02em;
        color: $headerTextColor;
        margin: $marginSize_10 0 0 0;
    }
    .btn {
        font-family: PoppinsBold, sans-serif;
        padding: 18px 0;
        width: 100%;
        color: white;
        height: 63px;
        background: $btnColor;
    }
    .fas {
        color: $btnColor;
        font-size: 12px;
    }
    .iconContainer {
        width: 20px;
        height: 15px;
        background: $pgBgColor;
        padding: 5px 10px;
        border-radius: 15px;
    }
    .grid_item {
        margin-top: $marginSize_20;
    }
    .overviewTxt {
        padding-top: $liTopMargin;
        font-family: PoppinsBold, sans-serif;
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 20px;
        letter-spacing: -0.02em;
        color: $headerTextColor;
    }
    .dropDownContainer {
        background: $white;
        padding: 10px 5px;
        width: 100px;
    }
    .marginTop {
        margin-top: $marginSize_20;
    }
    .dropDownContainer select, .dropDownContainer select:focus-visible {
        width: 100%;
        background: white;
        border: 1px solid $white;
        outline-color: $white;
    }
}

